.login-div div {
    padding: 8px 16px;
    margin: 8px 0px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 16px;
}
.login-div div label {
    font-size: 18px;
}
.login-div div input {
    height: 36px;
    width: 200px;
    margin-top: 16px;
    background: #b39ddb;
    border: none;
    font-size: 18px;
    padding-left: 16px;
    color: white;
}
.login-div div input:focus {
    outline: none;
}
