@media all and (max-width: 600px) {
    .user-header-text {
        font-size: 5vw;
        display: flex;
        align-items: Center;
    }
    .quiz-alert {
        top: -44px;
        left: 50%;
        transform: translateX(-50%);
        width: 90%;
    }
    .question {
        font-size: 5.5vw;
        white-space: nowrap;
    }
    .questions {
        padding: 24px 16px;
    }
    .question-div {
        width: 100%;
        padding: 0;
    }
    .login-register {
        flex-direction: column;
        text-align: right;
    }
    .login-register a {
        font-size: 4.5vw;
        width: auto;
        margin-top: 4px;
        margin-bottom: 4px;
    }
    .quiz-selection {
        position: initial;
        transform: none;
        justify-content: Center;
    }
    .quiz {
        width: 100vw;
        height: 60vw;
    }
    .answers div {
        width: 100%;
    }
}
