@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slide-in {
    from {
        top: -100px;
    }
    to {
        top: 0;
    }
}
@keyframes slide-out {
    from {
        top: 0;
    }
    to {
        top: -100px;
    }
}
@keyframes blica-blica {
    from {
        background: green;
    }
    to {
        background: #b39ddb;
    }
}

@keyframes roll-in {
    from {
        transform: rotateY(180deg);
    }
    to {
        opacity: 1;
        transform: rotateY(0deg);
    }
}
