.button {
    width: 130px;
    height: 36px;
    border-radius: 8px;
    border: none;
    color: white;
    cursor: pointer;
    background: #b39ddb;
}
.wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #7e57c2;
    padding: 24px;
}
.loading-text {
    text-align: center;
}

.loading {
    top: 50%;
    left: 50%;
    position: absolute;
    font-size: 24px;
    color: white;
    transform: translate(-50%, -50%);
    padding: 24px;
}
.button:disabled {
    background: #26016b;
    color: black;
    cursor: not-allowed;
}
